// app/ipo/[...slug]/page.js
''

import React from 'react';
import Navbar from '../../../components/Layout/Navbar';
import Footer from '../../../components/Layout/Footer';
import IpoDetailView from '../../../components/Modules/IpoDetailView';
import { IPO_LIST } from '../../../data/Data';

// Function to generate metadata (must be updated similarly)
export async function generateMetadata({ params }) {
    const idString = params.slug?.[0];
    
    // Convert to number only if idString exists and is numeric
    const idToLookup = idString && !isNaN(idString) ? parseInt(idString) : null;
    
    const ipo = IPO_LIST.find(p => p.id === idToLookup);
    const company = ipo ? ipo.company : 'IPO Detail';

    return {
        title: `${company} - IPO Dates, Price Band & GMP | FinIPO Hub`,
        description: `Detailed analysis, important dates, and financial summary for the ${company} IPO.`,
    };
}

const IpoDetailPage = ({ params }) => {
    const idString = params.slug?.[0]; 
    
    // ðŸŒŸ FIX: Check if idString is valid before parsing
    const idToLookup = idString && !isNaN(idString) ? parseInt(idString) : null;

    // Use the validated number for finding the IPO
    const ipo = IPO_LIST.find(p => p.id === idToLookup);

    if (!ipo) {
        // Return the 404 view, which you are already receiving
        return (
            <div className="min-h-screen pt-40 text-center bg-slate-50 dark:bg-slate-950">
                <h1 className="text-3xl font-bold text-red-500">404 - IPO Not Found</h1>
                <p className="mt-4 text-slate-600 dark:text-slate-400">
                    The requested IPO page could not be located.
                </p>
                <a href="/" className="inline-block mt-8 text-emerald-600 hover:underline">Go to Dashboard</a>
            </div>
        );
    }

    // If IPO is found, render the details
    return (
        <div className="min-h-screen font-sans bg-slate-50 dark:bg-slate-950 transition-colors duration-300">
            <Navbar />
            <IpoDetailView ipo={ipo} goBack={() => window.history.back()} />
            <Footer />
        </div>
    );
};

export default IpoDetailPage;